import React from "react";
import Dice from "./Dice";
import Modifier from "./Modifier";
import "./Users.css";

export default function Users(props) {
  const userDataArray = [];
  const buttonText = props.updatingHealth ? "Save" : "Edit";
  const buttonValue = props.updatingHealth ? "updating" : "save";
  const styleObj = props.user == "01000111 01100001 01101101 01100101 01110010 00100000 01000110 01110101 01100101 01101100" ?  {fontSize: "6px"} : {}
  props.data.map((data) => {
    if (props.user === data.roller) {
      const {
        roller,
        val,
        maxRoll,
        modifierValue,
        modifierClass,
        createdAt,
      } = data;
      if (modifierValue !== null) {
        userDataArray.push({
          roller,
          val,
          maxRoll,
          modifierValue,
          modifierClass,
          createdAt,
        });
      } else {
        userDataArray.push({
          roller,
          val,
          maxRoll,
          modifierValue: 0,
          modifierClass: "no mod",
          createdAt,
        });
      }
    }
  });

  return (
    <div>
      <p style={styleObj}>{props.user}</p>
      <Dice
        className="user-group"
        user={props.user}
        random={props.random}
      ></Dice>
      <Modifier user={props.user} handleChange={props.handleChange}></Modifier>
      <div class="input-group mb-3">
        {/* <input
          type="number"
          className={buttonValue}
          aria-describedby="basic-addon2"
          min="0"
        /> */}
        {/* <div className="input-group-append">
          <button
            value={`${buttonValue}-${props.user}`}
            className="btn btn-outline-success"
            type="button"
            onClick={props.updateHealth}
          >
            {buttonText}
          </button>
        </div> */}
      </div>

      <img
        alt="user-pic"
        id="parent-user-group"
        src={require(`../public/images/${props.image}.png`)}
      />
      {userDataArray.map((data) => (
        <div className="row justify-content-center">
          <p className="col-md-12" id="roll-history">
            <p id="rollRoller">
              {" "}
              <span>
                {data.val} / {data.maxRoll} {data.modifierClass} +{" "}
                {data.modifierValue} ={data.val + data.modifierValue}
              </span>
            </p>
          </p>
        </div>
      ))}
    </div>
  );
}
